/* All this css code is for the switch-checkbox*/ 
.switch {
  position: relative;
  display: inline-block;
  width: 55px;
  height: 20px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 30px;
  width: 30px;
  left: 0px;
  bottom: -4px;
  background-color: #3FB8DF;
  transition: .4s;
}

input:checked + .slider {
  background-color: #A2DFF1;
}

input:focus + .slider {
  box-shadow: 0 0 1px #A2DFF1;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
